import React from 'react';
import {Link} from "react-router-dom";

const ArticlesList = ({articles}) => (
    <>
        {
            articles.map((item)=>(
                <Link className="articles-list-item" key={item.name} to={`/article/${item.name}`}>
                    <h3>{item.title}</h3>
                    <p>
                        {
                            item.content[0].substring(0,5)
                        }
                    </p>
                </Link>
            ))
        }
    </>
)

export default ArticlesList;
