import React, {useEffect, useState} from 'react';
import {articles} from "../article-content";
import ArticlesList from "../components/ArticlesList";
import NotFoundPage from "./NotFoundPage";
import UpvotesSection from "../components/UpvotesSection";

const ArticlePage = ({match}) => {
    const name = match.params.name;

    const [article, setArticle] = useState({upvotes:0,comments:[]})


    const otherArticles = articles.filter((item)=>item.name!==name)

    useEffect(  ()=>{
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const  body = await result.json();
            console.log(body)
            setArticle(body)
        }
        fetchData().then()
    },[name])

    if(!article)return <NotFoundPage/>

    return (
        <>
            <h1>{article.title}</h1>
            <UpvotesSection articleName={name} upvotes={article.upvotes} setArticleInfo={setArticle}/>
            {
                article.comments.map((item) => (
                    <p key={item.username}>{item.text}</p>
                ))
            }
            <h3>Other Articles</h3>
            <ArticlesList articles={otherArticles}/>
        </>
    )
}

export default ArticlePage;
